
import { defineComponent } from 'vue';
import Cell from '@/components/Cell.vue';
import { Row } from '@/services/reversi';

export default defineComponent({
  props: {
    row: {
      type: Row,
      required: true,
    },
  },
  components: {
    Cell,
  },
  setup(props, context) {
    const onPlace = (x: number, y: number) => {
      context.emit('on-place', x, y);
    };

    return {
      onPlace,
    };
  },
});
